<template>


  <div class="container mt-lg-5">
    <div class="row">
      <div class="col m-1">
        <div class="card">
          <div class="card-header">
            <h1 class="text-center">Login</h1>
          </div>
          <div class="card-body">
            Email: <input id="em_inp_lgn" type="email" placeholder="email" class="form-control" v-model="email"><br>
            Password: <input type="password" placeholder="password" class="form-control" v-model="password"><br>

            <span>
              <p class="text-danger" v-if="is_login_error">{{ login_error_msg }}</p>
            </span>

            <div>
              <span v-if="logging_in">
                <button class="btn btn-outline-primary m-1" type="button" disabled>
                  <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span class="visually-hidden">Loading...</span>
                </button>
              </span>

              <span v-if="!logging_in">
                <input type="button" class="btn btn-outline-primary m-1" value="Login" @click="login">
              </span>

              <input type="button" class="btn btn-outline-primary m-1" value="Login by Google" @click="login_by_gmail">
            </div>

          </div>
        </div>
      </div>

      <div class="col-sm-1">
        <h3 class="text-center">OR</h3>

      </div>

      <div class="col m-1">
        <div class="card">
          <div class="card-header">
            <h1 class="text-center">Register</h1>
          </div>
          <div class="card-body">
            Email: <input type="email" placeholder="email" class="form-control" v-model="regi_email"><br>
            Password: <input type="password" placeholder="password" class="form-control" v-model="regi_password"><br>

            <span>
              <p class="text-danger" v-if="is_regi_error">{{ regi_error_msg }}</p>
            </span>

            <div>

              <span v-if="registering">
                <button class="btn btn-outline-primary m-1" type="button" disabled>
                  <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span class="visually-hidden">Loading...</span>
                </button>
              </span>

              <span v-if="!registering">
                <input type="button" class="btn btn-outline-primary m-1" value="Register" @click="register">
              </span>

              <input type="button" class="btn btn-outline-primary m-1" value="Register by Google" @click="login_by_gmail">
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>


</template>

<script>
import {
  getAuth,
  signInWithEmailAndPassword,
  GoogleAuthProvider,
  signInWithPopup,
  createUserWithEmailAndPassword
} from "firebase/auth"
import router from "@/router";

export default {
  name: "LoginView",
  data(){
    return {
      email: "",
      password: "",
      regi_email: "",
      regi_password: "",
      is_regi_error: false,
      is_login_error: false,
      regi_error_msg: "",
      login_error_msg: "",
      registering: false,
      logging_in:false
    }
  },
  methods:{
    login_by_gmail(){

      const provider = new GoogleAuthProvider();
      signInWithPopup(getAuth(), provider)
          .then((result)=>{
            console.log(result.user);
            router.push('/')
          })
          .catch((err)=>{
            alert(err.message)
          })
    },

    login(){
      this.logging_in = true;
      const auth = getAuth()

      signInWithEmailAndPassword(auth, this.email, this.password)
          .then((data)=>{
            console.log("Logged In!")
            console.log(data)
            this.logging_in = false;
            router.push('/')
          })
          .catch((err)=>{
            this.login_error_msg = err.message.replace("Firebase: ", "")
            this.is_login_error = true;
            this.logging_in = false;
          });
    },

    register(){
      this.registering = true;
      const auth = getAuth();
      createUserWithEmailAndPassword(auth, this.regi_email, this.regi_password)
          .then((userCredential) => {

            const user = userCredential.user;
            this.is_regi_error = true;
            console.log("Registered!", user.uid)
            this.registering = false;
            router.push('/')
          })
          .catch((error) => {

            this.is_regi_error = true;
            this.regi_error_msg = error.message.replace("Firebase: ", "");
            this.registering = false;
          });
    },

  },
  mounted() {


  }
}
</script>

<style scoped>
  .center {
    margin: auto;
  }

</style>