<template>


  <h1 class="m-3">New Blog</h1>

  <div class="m-5">
    <div class="alert alert-success" role="alert" v-if="published">
      Your Blog is Published Successfully!<br>
    </div>

    <div class="input-group input-group-lg">
      <span class="input-group-text" id="inputGroup-sizing-lg">Blog Title</span>
      <input type="text" class="form-control" aria-label="Sizing example input" aria-describedby="inputGroup-sizing-lg" v-model="blog_title">
    </div>

    <br>

    <div class="form-floating">
      <textarea rows="10" style="height:100%;" class="form-control" placeholder=" content here" id="floatingTextarea" v-model="blog_content"></textarea>
      <label for="floatingTextarea">Blog Content</label>
    </div>
    <br>
    <div>
      <button class="btn btn-outline-success btn-lg" @click="publish_blog" v-if="!publishing">Publish</button>
      <center>
        <div class="spinner-grow text-success" role="status" v-if="publishing">
          <span class="visually-hidden">Loading...</span>
        </div>
      </center>


    </div>

  </div>







</template>

<script>

import {getAuth, onAuthStateChanged} from "firebase/auth";
import router from "@/router";

// @ is an alias to /src
import {firestore_db} from "@/main";
import { collection, addDoc } from "firebase/firestore";

export default {
  name: "WriteBlog",
  mounted() {
    this.auth = getAuth();
    onAuthStateChanged(this.auth, (user)=>{
      if(!user)
        router.push('/login');
    })
  },
  data(){
    return{
      blog_title: '',
      blog_content: '',
      publishing: false,
      published: false
    }
  },
  methods:{
    async publish_blog(){

      // Add a new document with a generated id.
      let current_user = getAuth().currentUser;
      this.publishing = true;

      let new_blog = await addDoc(collection(firestore_db,"blogs"), {
        author: (current_user.displayName!=null)?current_user.displayName:current_user.email,
        title: this.blog_title,
        content: this.blog_content,
        timestamp: Math.floor(Date.now() / 1000),
        uid: current_user.uid
      });

      console.log("new blog added with ID: ", new_blog.id);
      this.published = true;
      this.publishing = false;
      await router.push('/')

    }
  }
}
</script>

<style scoped>

</style>